* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'PT Sans', sans-serif;
}

.html {
  scroll-behavior: smooth;
}
.home {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}
* {
  color: #C5C6C7;
}

.techno-hunt {
  background-color: black;
  padding: 1em;
  align-items: center;
  justify-content: center;
}
.innovation {
  background-color: black;
  padding: 1em;
  align-items: center;
  justify-content: center;
  padding-left: 18em;
  padding-right: 18em;
}
h1.techno-hunt {
  padding-left: 18em;
  color: aqua;
}
h1.innovation {
  padding-left: 12em;
  color: aqua;
}
h3.tech {
  padding-left: 15em;
  color: aqua;
}
h3.innovation {
  padding-left: 0em;
  color: aqua;
}
p.tech {
  padding-left: 18em;
  padding-right: 14em;
}
ul {
  /* padding-left: 2rem; */
  list-style-type: none;
}

ul.innovation li {
  padding-left: 1.5em;
  font-size: 20px;
  background-image: url("./components/star.png");
  background-size: 1.8rem 1.8rem;
  background-repeat: no-repeat;
}

ul.rules li {
  
  padding-left: 1.5em;
  background-image: url("./components/arrow.svg");
  background-size: 1.2rem 1.2rem;
  background-repeat: no-repeat;
}
.events {
  padding: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;
}
.bold {
  color: aqua;
} 
table {
  border: 2px solid aqua;
  width: 800px;
  height: 200px;
}
  
th {
  border: 1px solid black;

}
  
td {
  text-align: center;
  padding: 1rem;
}

@media screen and (max-width: 960px){
  .techno-hunt {
    background-color: black;
    padding: 1em;
    align-items: center;
    justify-content: center;
  }
  .innovation {
    background-color: black;
    padding: 1em;
    align-items: center;
    justify-content: center;
  }
  h1.techno-hunt {
    padding-left: 1em;
    font-size: 25px;
  }
  h1.innovation {
    padding-left: 0.5em;
    color: aqua;
  }
  h3.tech {
    font-size: 18px;
    padding-left: 0.5em;
    color: aqua;
  }
  h3.innovation {
    padding-left: 0em;
    color: aqua;
  }
  p.tech {
   padding-left: 0.5em;
   padding-right: 0.5em;
  }
  ul {
    /* padding-left: 2rem; */
    list-style-type: none;
  }
  
  ul.innovation li {
    padding-left: 1.5em;
    font-size: 20px;
    background-image: url("./components/star.png");
    background-size: 1.8rem 1.8rem;
    background-repeat: no-repeat;
  }
  
  ul.rules li {
    
    padding-left: 1.5em;
    background-image: url("./components/arrow.svg");
    background-size: 1.2rem 1.2rem;
    background-repeat: no-repeat;
  }
}